.choosePhoto {
    margin-top:1em;
}
.choosePhoto img {
    cursor: pointer;
    padding: .25em;
    width: calc((1/8) * 100%);
}
.selectedAvatar {
    padding:2em;
}
.avatarControls button {
    border:none;
    border-radius: 50%;
    height:2em;
    width:2em;
}
.selectedAvatar svg {
    width: 33.33%;
    float: left;
}
.avatarControls {
    width: 62.66%;
    margin-left:4%;
    float: left;
    font-size: .75em;
}
.saveBtn {
    border: none;
    clear: both;
    display: block;
    margin: auto;
    padding: .25em 1em;
}